@font-face {
  font-family: 'Proxima Nova';
  src: url('../../fonts/Proxima-Nova/ProximaNova-Light.woff2') format('woff2'),
    url('../../fonts/Proxima-Nova/ProximaNova-Regular.woff2') format('woff2'),
    url('../../fonts/Proxima-Nova/ProximaNova-Semibold.woff2') format('woff2');
}
.statistics {
  font-family: 'Proxima Nova';
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {
  font-size: 16px;
  font-weight: 600;
  text-align: center;
  text-transform: uppercase;
}

.statList {
  display: flex;
  padding-inline-start: 0px;
}

.item {
  width: 62px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #ffffff;
  padding-left: 16px;
  padding-right: 16px;
}

.labe {
  font-size: 12px;
  line-height: 20px;
  margin-top: 10px;
  font-weight: 300;
}

.percentage {
  font-size: 18px;
  line-height: 20px;
  margin-bottom: 10px;
  font-weight: 400;
}
