@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap');

.profile {
  width: 250px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 50px;
  font-family: 'Lato';
  font-size: 12px;
  line-height: 32px;
  font-weight: 400;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 4px;
  overflow: hidden;
  filter: drop-shadow(0px 1px 1.5px rgba(191, 205, 222, 0.75));
}

.description {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 22px;
}

.avatar {
  overflow: hidden;
  width: 100px;
  height: 100px;
  border: 1px;
  border-radius: 50px;
  background-color: blue;
  margin-top: 32px;
  margin-bottom: 18px;
}

.name {
  margin: 0px;
  padding: 0px;
  font-size: 18px;
  font-weight: 700;
  color: #122236;
}

.tag,
.location {
  margin: 0px;
  padding: 0px;
  font-size: 14px;
  color: #768696;
}

.stats {
  list-style-type: none;
  display: flex;
  padding-inline-start: 0px;
  margin: 0px;
  background-color: #f3f6f9;
}

.stats li {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 84px;
  border-right: 1px solid #e4e9f0;
}
.stats li:last-child {
  border-right: none;
}

.label {
  font-size: 12px;
  color: #768696;
  margin: 0px;
  padding: 0px;
}

.quantity {
  font-size: 14px;
  color: #1f3349;
  font-weight: 900;
}
